@use "../variables.scss";


.welcome-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 1.5rem;

  flex-basis: 40%;
  max-width: 50rem;
  border-radius: 0.5rem;
  padding: 2rem;

  z-index: 1;

  background-color: variables.$blue-small;

  .welcome-title {
    font-size: 2rem;
    color: variables.$blue-dark;
  }

  .welcome-message {
    font-size: 1rem;
    color: variables.$blue-dark;
  }
}